    onClickParagraph(index) {
      if (this.faq2[index].hide.display === 'block') {
        this.faq2[index].hide.margin = '0px';
        this.faq2[index].hide.transform = 'rotate(0deg)';
        this.faq2[index].hide.padding = '0px';
