<template>
  <div class="login">
    <div class="login__content">
      <div class="login__title">   </div>
      <div class="login__body">
        <div class="login__error" v-if="error">: <span v-html="error"></span></div>
        <div class="login__input">
          <p class="login__input-name">
            
            <span class="login__input-name-require">*</span>
          </p>
          <input class="login__input-value" type="text" name="iin" v-model="iin">
        </div>
        <div class="login__input">
          <p class="login__input-name">
             
            <span class="login__input-name-require">*</span>
          </p>
          <input class="login__input-value" type="password" name="password" v-model="password">
        </div>
        <div class="login__links">
          <p> ?
            <router-link to="/step-1">
              <span></span>
            </router-link>
          </p>
          <p>
            <router-link to="/forgot-password"> ?</router-link>
          </p>
        </div>
      </div>
      <div class="login__footer">
        <input class="login__input-submit" type="button" value="" v-on:click="submitRequestLogin">
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      iin: '',
      password: '',
      error: '',
      errorIIN: '',
      errorPassword: '',
    }
  },
  mounted() {
    if (localStorage.getItem('logged')) {
      this.$router.push({path: '/notifications'});
    }
  },
  methods: {
    submitRequestLogin() {
      /*
      let formData = new FormData();
      formData.append('iin', this.iin);
      formData.append('password', this.password);
      axios().post('signIn', formData, { headers: { 'Content-Type': 'application/json' } })
      .then(response => {
        console.log(response.data);
      })
      .catch(error => {
        console.log(error);
      })
      */
      this.error = '';
      if (this.validateLogin()) return;
      const axios = require('axios');
      axios.post('https://crediter.kz/api/signIn', { 'iin': this.iin, 'password': this.password })
          .then(async response => {
            if (response.data.success) {
              await localStorage.setItem('token', this.token);
              await localStorage.setItem('logged', 'true');
              this.$router.push({path: '/notifications'});
            }
            else {
              this.error = response.data.message;
            }
          })
          .catch(error => {
            this.error = error;
          })
    },
    validateLogin() {
      if (!this.iin) {
        this.error = '    ';
      }
      else if (this.iin.length !== 12) {
        this.error = ' ';
      }
      else if (!this.password) {
        this.error = '    ';
      }
      else return false;
      return true;
    },
  },
}
</script>

<style>
@import url('/css/block/login.css');
@import url('/css/block/mobile/login.css');
</style>
