<template>
  <div class="faq">
    <div class="faq__content">
      <div v-for="(question,index) in faq"
           :class="'faq__paragraph '+((index===0)?'faq__paragraph-first':'')+((index===faq.length-1)?'faq__paragraph-last':'')"
           :key="'UUU'+index"
      >
        <div :class="'faq__paragraph-header '+((index===0)?'faq__paragraph-header-first':'')+((index===faq.length-1)?'faq__paragraph-header-last':'')"
             v-on:click="onClickParagraph(index)"
        >
          <span class="faq__paragraph-title">{{ question.title }}</span>
          <img class="faq__paragraph-arrow" src="/images/arrow.png" :style="'transform: '+question.hide.transform">
        </div>
        <div class="faq__paragraph-body">
          <div class="faq__paragraph-body-content" :style="'padding: '+question.hide.padding">
            <div class="faq__paragraph-body-content-inside" :style="'display: '+question.hide.display" v-html="question.inside">
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      faq: [
        {
          title: '    ?',
          inside: '<span> (, )                 ,       .</span>',
          hide: {
            transform: 'rotate(0deg)',
            padding: '0px',
            display: 'none'
          },
        },
        {
          title: '      ?',
          inside: '<ul>'+
            '<li>       ()  ,  ()  ,   </li>'+
            '<li> ,  ,     ,     ,   ,        </li>'+
            '<li>  ,      ,     ,       ,          </li>'+
            '<li>             ()  ,  ()  , , ,  , ,              </li>'+
            '<li> ,     ,    ,     </li>'+
            '<li>  (    ),        ,   ,     </li>'+
            '<li>    ,  </li>'+
            '<li>     <a href="https://online.zakon.kz/Document/?doc_id=1012633"></a>   ,     ()  ,  ()  ,   ,    .</li>'+
            '</ul>',
          hide: {
            transform: 'rotate(0deg)',
            padding: '0px',
            display: 'none'
          },
        },
        {
          title: '   ,     ?      ?',
          inside: '<p>       ()  ,  ()  ,              , :</p>'+
            '<ul>'+
            '<li>          </li>'+
            '<li> </li>'+
            '<li> () ,   -      ( ),  -      ( )</li>'+
            '<li>,       </li>'+
            '<li>  </li>'+
            '</ul>'+
            '<p>2.       ()  ,  ()  ,              ,  :</p>'+
            '<p>   ,       ,  :</p>'+
            '<ul>'+
            '<li>'+
            '<p>     ():</p>'+
            '<p>          </p>'+
            '<p>   </p>'+
            '<p>, ,  (    ,  )   ,     </p>'+
            '<p> </p>'+
            '<p>      </p>'+
            '<p> ,       , , ,  (, ),         </p>'+
            '<p>    ,         ,   ,        ,         .</p>'+
            '</li>'+
            '<li>   8.00  21.00            ,    ,                         </li>'+
            '<li>              8.00  21.00                 ,    ,      (, )    </li>'+
            '<li>       8.00  21.00           </li>'+
            '<li>    ,   ,   ,           ,   ,       <a href="https://online.zakon.kz/Document/?doc_id=1012633"></a>   </li>'+
            '<li>      -    </li>'+
            '</ul>'+
            '<p>   ,       ,  :</p>'+
            '<ul>'+
            '<li>   ,</li>'+
            '<li>       ,   , ,  (    ,  ),    ,   ,</li>'+
            '<li>        ,    .</li>'+
            '</ul>',
          hide: {
            transform: 'rotate(0deg)',
            padding: '0px',
            display: 'none'
          },
        },
        {
          title: '     ,   ?',
          inside: '<span>     ,            ,        .          .       ,  .</span>',
          hide: {
            transform: 'rotate(0deg)',
            padding: '0px',
            display: 'none'
          },
        },
        {
          title: '       ?',
          inside: '<span>    .   ,  ,      .</span>',
          hide: {
            transform: 'rotate(0deg)',
            padding: '0px',
            display: 'none'
          },
        },
        {
          title: '           ?',
          inside: '<p>      ,   .     ,     ,        ,        . ,    ,      3%  25%     .</p>'+
              '<p>    ,      ,      .</p>'+
              '<p>            .</p>',
          hide: {
            transform: 'rotate(0deg)',
            padding: '0px',
            display: 'none'
          },
        },
      ]
    }
  },
  methods: {
    onClickParagraph(index) {
      if (this.faq[index].hide.display === 'block') {
        this.faq[index].hide.transform = 'rotate(0deg)';
        this.faq[index].hide.padding = '0px';
        setTimeout(()=>{this.faq[index].hide.display = 'none'},250);
      }
      else {
        this.faq[index].hide.transform = 'rotate(180deg)';
        this.faq[index].hide.padding = '20px 0px 20px 0px';
        setTimeout(()=>{this.faq[index].hide.display = 'block'},250);
      }
    },
  }
}
</script>

<style>
@import '/./css/faq2.css'
</style>

